"use client";
import { AllListsView } from '../../views/AllListsView';
import { useAuth } from '../../context/AuthContext';
import { useRouter } from 'next/navigation';
import { useEffect } from 'react';
import { Loader2 } from 'lucide-react';
import { LandingView } from '../../views/LandingView';

export default function ListsPage() {
    const { user, appUser, loading, logout } = useAuth();
    const router = useRouter();

    if (loading) {
        return (
            <div className="min-h-screen flex items-center justify-center bg-surface">
                <Loader2 className="text-primary animate-spin" size={48} />
            </div>
        );
    }

    if (!user) {
        return <LandingView onLoginSuccess={() => router.push('/lists')} />;
    }

    return (
        <AllListsView
            userId={user.uid}
            userName={user.displayName}
            userEmail={user.email}
            isAdmin={appUser?.role === 'admin'}
            onLogout={logout}
            // Temporarily no-op or redirect to dashboard/events if needed
            onBack={() => router.push('/')}
            onNavigateToHelp={() => { }} // TODO: Route to help
        />
    );
}
